* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Container for the whole app */
.App {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* Background and particles */
#particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

/* Ensure the top container fills half of the screen */
.top-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  text-align: center;
  z-index: 1;
  height: 100%;
  width: 100%;
}

/* Ensure the bottom container (boxes) fills the remaining space */
.bottom-container {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  z-index: 1;
}

.hero-text {
  font-size: 3rem;
  color: rgb(255, 255, 255);
}

/* Boxes container styling */
.boxes-container {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  /* Ensures space between boxes */
  width: 100%;
  margin-top: 3rem;
  flex-grow: 1;
}

/* Individual box styling */
.box {
  flex: 1;
  height: 100;
  margin: 1rem;
  background-color: rgb(255, 255, 255);
  border: 2px solid #000000;
  border-radius: 15px;
  flex-direction: column;
  /* Align content vertically */
  justify-content: space-between;

  color: rgb(0, 0, 0);
  text-align: left;
  padding: 1.5rem;
  position: relative;
  /* Required for positioning the icons */
}

/* Box title container with icons */
.box-title-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.box-subtitle {
  font-size: 1.2rem;
  color: #555; /* Subtle gray color for the subtitle */
  font-family: 'Red Hat Display', sans-serif;
  font-weight: 400;
  margin-top: 0.5rem;
  white-space: nowrap;
}
.box-title {
  font-size: 1.8rem;
  font-family: 'Red Hat Display', sans-serif;
  font-weight: 600;
  color: rgb(0, 0, 0);
}

.box-icons {
  display: flex;
  gap: 0.5rem;
}

.box-icons img {
  width: 2.5rem;
  height: 2.5rem;
}

/* Line under the title */
.line {
  width: 100%;
  height: 1px;
  background-color: rgb(0, 0, 0);
  margin-top: 1rem;
}
.sub{
  color:rgb(155, 152, 152);

}

.rainbow {
  text-align: center;
  text-decoration: underline;
  font-size: 20px;
  font-family: monospace;
  letter-spacing: 5px;
  margin-bottom: 1rem;


}

.rainbow_text_animated {
  background: linear-gradient(to right, #6666ff, #0099ff, #00ff00, #ff3399, #6666ff);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  animation: rainbow_animation 6s ease-in-out infinite;

}

@keyframes rainbow_animation {

  0%,
  100% {
    background-position: 0 0;
  }

  50% {
    background-position: 100% 0;
  }
}

/* Content inside the box */
.box-content {
  margin-top: 1.5rem;
}

/* Media queries */
@media (max-width: 960px) {
  .boxes-container {
    flex-direction: column;
    align-items: center;
    /* Center align boxes on smaller screens */
  }

  .box {
    margin: 1rem 0;
    /* Add margin between stacked boxes */
  }

  .box-title {
    font-size: 1.5rem;
  }

  .box-icons img {
    width: 2rem;
    height: 2rem;
  }
}